:root{
  --padding-container: 100px 0;
  --color-title: #001A49;
  }



.container{
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
  padding: var(--padding-container );
}

.hero{
  width: 100%;
  height: 100vh;
  min-height: 600px;
  max-height: 800px;
  position: relative;
  display: grid;
  grid-template-rows: 100px 1fr; 
  color: #fff;

    
}


.hero::before{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;  
  background-image: linear-gradient(180deg, #0000008c 0%, #0000008c 100% ),url('./Imagenes/estud_c_robot.jpg');
  background-size: cover;
  clip-path: polygon(0 0, 100% 0, 100% 80%, 50% 95%, 0 80%); 
  z-index: -1;

}
  /*Nav*/
.nav_container{
  --padding-container:0;
  height: 100%;
  display: flex;
  align-items: center;
  
 
}

.nav_title{
  font-weight: 300;
  font-weight: 700;

}
.nav_items{
  list-style: none;
}

.nav_links{
  color: white;
  text-decoration: none;

}
.nav_link{
  margin-left: auto;
  color: #fff;
  padding: 0;
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: max-content;
  gap: 2em;
  

}
.nav_link a:hover{
  color: #f0f0f0; /* Cambia el color a tu gusto */
  background-color: #2091f9; /* Puedes agregar un fondo si lo deseas */
  text-decoration: underline; /* Agrega una subrayado si lo prefieres */  
 
}

.nav_menu{
  margin-left: auto;
  cursor: pointer;
  display: none;
}

.nav_close{
  display: var(--show, none);
}

.nav_img{
  display: block;
  width: 30px;
}

.hero_container{
  max-width: 800px;
  display: grid;
 --padding-container: 0; 
 grid-auto-rows: max-content;
 align-content: center;
 gap: 1em;
 padding-bottom: 100px;
 text-align: center;
}


.hero_title{
  font-size: 2.5rem;
}
.hero_paragraph{
 margin-bottom: 15px;
 line-height: 1.5;

}

.cta{
  display: inline-block;
  background-color: #2091f9;
  justify-self: center;
  color: #fff;
  text-decoration: none;
  padding: 13px 30px;
  border-radius: 32px;
  
}

.cta:hover{
  color: #2091f9; /* Cambia el color a tu gusto */
  background-color: aqua; /* Puedes agregar un fondo si lo deseas */
  text-decoration: underline; /* Agrega una subrayado si lo prefieres */  
 
}


.cta_2{
  display: inline-block;
  background-color: #f99420;
  justify-self: center;
  color: #fff;
  text-decoration: none;
  padding: 13px 30px;
  border-radius: 32px;
}

.cta_2:hover{
  color: red; /* Cambia el color a tu gusto */
  background-color: #f9b420; /* Puedes agregar un fondo si lo deseas */
  text-decoration: underline; /* Agrega una subrayado si lo prefieres */  
 
}


/*about*/

.about{
  text-align: center;
}

.subtitle{
  color: var(--color-title);
  font-size: 2rem;
  margin-bottom: 25px;
}



.about_paragraph{
  line-height: 1.7;
  

}
.about_main{
  padding-top: 80px;
  display: grid;
  width: 90%;
  margin: 0 auto;
  gap: 1em;
  overflow: hidden;
  grid-template-columns: repeat(auto-fit, minmax(260px, auto));
}

.about_icons{
  display: grid;
  gap: 1em;
  justify-items: center;
  width: 260px;
  overflow: hidden;
  margin: 0 auto; 

}
.about_icon{
  width: 40px;
}

/*knowlege*/
.knowledge{
  background-color: #e5e5f7;
  background-image:  radial-gradient(#444cf7 0.5px, transparent 0.5px), radial-gradient(#444cf7 0.5px, #e5e5f7 0.5px);
  background-size: 20px 20px;
  background-position: 0 0,10px 10px;
  overflow: hidden;

}
.knowledge_container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1em;
  align-items: center;

}

.knowledge_picture{
  max-width: 500px;

}

.knowledge_img{
  width: 100%;
  display: block;
}
.knowledge_paragraph{
  line-height: 1.7;
  margin-bottom: 15PX;
  margin-top: 15px;
  text-align: justify;
}

/*question*/
.cuestion_offer{ 
  
}
.cuestion_container{    
  align-items: center;
  align-content: center;
  gap: 1em;
  padding-bottom: 20px;
  text-align: center;
  justify-items: center;
}

.cuestion_paragraph{
 

  text-align: center;
  line-height: 1.7;
  margin-bottom: 15PX;
  margin-top: 15px;
  
}

/* Testimonios */

.testimonio{
  background-color: #e5e5f7;

 
}

.testimonio_container{
  display: grid;
 grid-template-columns: 50px 1fr 50px;
 gap: 1em;
 align-items: center;

 
}
.testimonio_Titulo{
  margin-bottom: 1em;
  color: slateblue;
}
.testimonio_body{
  display: grid;
  grid-template-columns: 1fr max-content;
  justify-content: space-between;
  align-items: center;
  gap: 2em;
  grid-column: 2/3;
  grid-row: 1/2;
  opacity: 0;
  pointer-events: none;
}
.testimonio_body--show{
  pointer-events: unset;
  opacity: 1;
  transition: opacity 1.5s ease-in-out;

}


.testimonio_img{
  width: 300px;
  height: 300px;
  border-radius: 50%;
  object-fit: cover;
  object-position: 50% 30%;
}

.testimonio_text{
  max-width: 700px;
  line-height: 1.5;

}
.testimonio_course{
  background-color: royalblue;
  color: #fff;
  display: inline-block;
  padding: 5px;
  font-size: 20px;
}
.testimonio_review{
  text-align: justify;
}

.testimonio_awr{
  width: 90%;
  cursor: pointer;
}

/*Cuestions 2*/

.cuestions{
  text-align: center;

}

.cuestions_container{
  display: grid;
  gap: 2em;
  padding-top: 50px;
  padding-bottom: 100px;

}

.cuestions_padding{
  padding: 0;
  transition: padding .5s;
  border: 1px solid #5454D4;
  border-radius: 6px;


}

.cuestions_padding--add{
  padding-bottom: 30px;

}

.cuestions_answer{
   padding: 0 30px 0;
   overflow: hidden;
}

.cuestions_title{
  display: flex;
  /*margin-bottom: 100px;*/
  font-size: 20px;
  padding: 30px 0 30px;
  cursor: pointer;
  color: var(--color-title);
  justify-content: space-between;
}
.cuestions_arrow{
   border-radius: 50%;
   background-color: var(--color-title);
   width: 25px;
   height: 25px;
   display: flex;
   justify-content: center;
   align-items: center;
   align-self: flex-end;
   margin-left: 10px;
   transition: transform .3s;
}
.cuestions_arrow--rotate{
  transform: rotate(180deg);

}

.cuestions_show{
  text-align: justify;
  line-height: 1.5;
  height: 0;
 /* margin-top: 100px;*/
  transition: height .3s;
}

.cuestions_img{
  display: block;
}

/*fotter*/
.footer{
  background-color: #1d293f;

}

.footer_title{
   font-weight: 100;
   font-size: 2rem;
   margin-bottom: 150px; 
   color: #fff;
}

.footer_newsletter{
  color: #fff;
}

.footer_container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
  padding-bottom: 60px;
}
.nav_link{
  margin-right: 200px;
}
.nav--footer{
  padding-bottom: 20px;
  font-display: grid;
  gap: 1em;
  grid-auto-flow: row;
  height: 100%;
}

.nav_link_footer{
  display: flex;
  margin: 0;
  margin-right: 20px; 
  flex-wrap: wrap;
}
.footer_inputs{
  margin-top: 10px;
  display: flex;
  overflow: hidden;
}

.footer_input{
  background-color: #fff;
  height: 40px;
  width: 450px;
  display: block;
  padding-left: 10px;
  border-radius: 6px;
  font-size: 2rem;
  outline: none;
  border: none;
  margin-right: 16px;
  margin-top: 10px;
   

}
.footer_submit{
  margin-left: auto;
  display: inline-block;
  height: 50px;
  padding: 0 20px;
  background-color: #2091f9;
  border: none;
  font-size: 1rem;
  color: #fff;
  border-radius: 6px;
  margin-top: 10px;

}

.footer.copy{
  --padding-container: 30px 0;
  text-align: center;
  color: #fff;
  align-items: center;
}
.footer_copyright{
   font-weight: 300;
   color: #fff;
   align-items: center;

}
.footer_icons{
  display: inline-flex;
  margin-bottom:  10px;

}
.footer_img{
  width: 30px;

}

/*Media Queries*/
@media (max-width:800px){
  
  .nav_menu{
    
    display: block;
  }

   .nav_link--menu{
    position: fixed;
    background-color: black; 
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    z-index: 100;
    opacity: 0;
    pointer-events: none;
    transition: .7s opacity;
   }

   .nav_link--show{
    --show: block;
    opacity: 1;
    pointer-events: unset;
    
   }
   .nav_close{
    position: absolute;
    top: 30px;
    right: 30px;
    width: 30px;
    cursor: pointer;
   }
   .hero_title{
    font-size: 2.5rem;
   }

   .about_main{
    gap: 2em;
   }

   .about_icons:last-of-type{

      grid-column: 1/-1;
   }
   .knowledge_container{
    grid-template-columns: 1fr;
    grid-template-rows: max-content 1fr;
    gap: 3em;
    text-align: center;
   }

   .knowledge_picture{
    grid-row: 1/2;
    justify-self: center;

   }

   .testimonio_container{
    grid-template-columns: 30px 1fr 30px;

   }
   .testimonio_body{
    grid-template-columns: 1fr;
    grid-template-rows: max-content max-content;
    gap: 3em;
    text-align: center;
    justify-items: center;
   }

   .testimonio_img{
    width: 250px;
    height: 250px;
   }

   .footer_container{
    flex-wrap: wrap;
    align-items: center;
   }
   .nav--footer{
    width: 100%;
    justify-items: center;
   }

   .nav_link--footer{
    width: 100%;
    justify-content: space-evenly;
    margin: 0;

   }

   .footer_form{
    width: 100%;
    justify-content: space-evenly;
   }

   .footer_input{
    flex: 1;
   }

